import { Typography } from "@mui/material";
import { useEffect, useState } from "react";
import './about.css';
import MemberBrief from "../../components/MemberBrief";
import avatar from '../../public/images/avatar.jpg'
import { MemberDetails } from "../../components/memberDetails";
import axios, { Routes } from '../../services/axios';
import Meta from '../../Meta';

export default function About() {

    let [members, setMembers] = useState([]);
    let [member, setMember] = useState({});
    let [selected, setSelected] = useState(false);
    let [loading, setLoading] = useState(false);

    useEffect(async () => {
        setLoading(true);
        const { url, method } = Routes.api.about();
        const { data } = await axios[method](url);
        setLoading(false);
        setMembers(data);
    }, [])

    function memberClicked(member) {
        setMember(member);
        setSelected(true);
    }

    return (
        <div className="about-wrapper">
            <Meta title={"About US"} description={"About Finance Mantra"} keywords={"Pankil Parikh, Meet Patel, Parthkumar Prajapati, Budget"} />
            <h1>About Us</h1>
            <div className="about-info">
                <Typography paragraph>
                    We are group of 3 people. We develop a budget app to manage money. We design this website in such a way that everyone can access this website.It is a free of cost website.
                    user can add income expense in this website. User can add category of income expense as well as all other naccesary information of the income and expense. User can add method of income and expense this is a unique feature of the website.
                    User can also see graphical  view of Income and Expense with the use of line chart and Pie chart
                </Typography>
                <Typography paragraph>
                    In tis budget app we add one donation link also through which any user can donate some amount which we can use in our future development.This a the first version of our app. Some features are in developing state which we can update in our next version. In which user can also calculate EMI and loan through our app.We are also planing to add one feature through which you can manage your bank accounts and save money.As there is a quote "Save Money And Money Will Save You."
                </Typography>

            </div>
            <br></br>

            {loading ? <div>Loading...</div> : !selected ?
                <div className="team-container">
                    {members.map(member =>
                        <MemberBrief onClick={memberClicked} member={member} image={member.image}></MemberBrief>
                    )}
                </div> :
                <MemberDetails member={member} onClick={() => { setSelected(false) }}></MemberDetails>
            }


        </div>
    )
}